


































import { Component, Vue } from 'vue-property-decorator';
import { Header, Loading } from '@/components';
import { getTrackInfo } from '../api';

@Component({
  components: {
    YHeader: Header,
    Loading,
  },
  name: 'detail',
})
export default class MelodyDetail extends Vue {
  public $refs !: {
    content: Element,
    audio: HTMLAudioElement,
  };

  private created() {
    this.loadData(+this.$route.params.id);
  }

  private data() {
    return {
      data: null,
    };
  }

  get isPlay() {
    return this.$store.state.player.isPlay;
  }

  private play() {
    this.$store.commit('player/add', this.data);
    this.$store.commit('player/play');
  }

  private pause() {
    this.$store.commit('player/pause');
  }

  private async loadData(id: number) {
    const res = await getTrackInfo(id);
    this.data = res;
  }

  private goBack() {
    this.$router.back();
  }
}
